"use client";

import { useState, useEffect } from "react";
import {
  Search,
  Download,
  Calendar,
  ArrowLeft,
  ChevronDown,
  ChevronRight,
  Eye,
  Upload,
  CheckCircle,
  Clock,
  AlertCircle,
  MapPin,
  TrendingUp,
  Users,
  FileText,
} from "lucide-react";
import axios from "axios";

// Stats Cards Component
const StatsCards = () => {
  const [stats, setStats] = useState({
    totalApplications: 0,
    currentlyQualified: 0,
    activeCompanies: 0,
    totalPlacements: 0,
  });

  useEffect(() => {
    fetchStats();
  }, []);

  const fetchStats = async () => {
    try {
      const { data } = await axios.get(
        `http://localhost:5000/round_tracking/stats/2022`
      );
      setStats(data);
    } catch (error) {
      console.error("Error fetching stats:", error);
    }
  };

  return (
    <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mt-6">
      <div className="bg-blue-50 p-4 rounded-lg">
        <p className="text-sm text-blue-600 font-medium">Total Applications</p>
        <p className="text-2xl font-bold text-blue-900 mt-1">
          {stats.totalApplications}
        </p>
      </div>
      <div className="bg-amber-50 p-4 rounded-lg">
        <p className="text-sm text-amber-600 font-medium">
          Currently Qualified
        </p>
        <p className="text-2xl font-bold text-amber-900 mt-1">
          {stats.currentlyQualified}
        </p>
      </div>
      <div className="bg-emerald-50 p-4 rounded-lg">
        <p className="text-sm text-emerald-600 font-medium">Active Companies</p>
        <p className="text-2xl font-bold text-emerald-900 mt-1">
          {stats.activeCompanies}
        </p>
      </div>
      <div className="bg-purple-50 p-4 rounded-lg">
        <p className="text-sm text-purple-600 font-medium">Total Placements</p>
        <p className="text-2xl font-bold text-purple-900 mt-1">
          {stats.totalPlacements}
        </p>
      </div>
    </div>
  );
};

// Filter Bar Component
const FilterBar = ({ filters, onFiltersChange }) => {
  return (
    <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
      <div className="flex flex-col lg:flex-row gap-4">
        <div className="flex-1">
          <div className="relative">
            <Search className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
            <input
              type="text"
              placeholder="Search companies or positions..."
              className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              value={filters.search}
              onChange={(e) =>
                onFiltersChange({ ...filters, search: e.target.value })
              }
            />
          </div>
        </div>
        <div className="lg:w-48">
          <select
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            value={filters.status}
            onChange={(e) =>
              onFiltersChange({ ...filters, status: e.target.value })
            }
          >
            <option value="all">All Status</option>
            <option value="upcoming">Upcoming</option>
            <option value="ongoing">Ongoing</option>
            <option value="completed">Completed</option>
          </select>
        </div>
        <div className="lg:w-48">
          <select
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            value={filters.jobType}
            onChange={(e) =>
              onFiltersChange({ ...filters, jobType: e.target.value })
            }
          >
            <option value="all">All Job Types</option>
            <option value="internship">Internship</option>
            <option value="full_time">Full Time</option>
            <option value="internship_plus_ppo">Internship + PPO</option>
          </select>
        </div>
      </div>
    </div>
  );
};

// Result Upload Modal
const ResultUploadModal = ({
  eventId,
  onClose,
  companyName,
  positionTitle,
  onResultsUpdated,
}) => {
  const [uploadMethod, setUploadMethod] = useState("manual");
  const [students, setStudents] = useState([]);
  const [selectedStudents, setSelectedStudents] = useState(new Set());
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchEventStudents();
  }, [eventId]);

  const fetchEventStudents = async () => {
    try {
      setLoading(true);
      const response = await axios.get(
        `http://localhost:5000/round_tracking/events/${eventId}/students?status=attended`
      );
      if (response.data.success) {
        setStudents(response.data.students);
      }
    } catch (error) {
      console.error("Error fetching students:", error);
      setError("Failed to load students");
    } finally {
      setLoading(false);
    }
  };

  const handleStudentToggle = (studentId) => {
    const newSelected = new Set(selectedStudents);
    if (newSelected.has(studentId)) {
      newSelected.delete(studentId);
    } else {
      newSelected.add(studentId);
    }
    setSelectedStudents(newSelected);
  };

  const handleFileUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = async (e) => {
      try {
        const csvText = e.target.result;
        const lines = csvText.split("\n").filter((line) => line.trim());
        const csvData = lines.slice(1).map((line) => line.trim());

        const response = await axios.post(
          `http://localhost:5000/round_tracking/events/${eventId}/upload-csv`,
          { csvData }
        );
        if (response.data.success) {
          onResultsUpdated?.();
          onClose();
        }
      } catch (error) {
        setError(error.response?.data?.message || "Failed to process CSV");
      }
    };
    reader.readAsText(file);
  };

  const handleSubmit = async () => {
    try {
      setLoading(true);
      setError(null);

      const qualifiedRegistrationNumbers = students
        .filter((student) => selectedStudents.has(student.id))
        .map((student) => student.registration_number);

      const response = await axios.post(
        `http://localhost:5000/round_tracking/events/${eventId}/results`,
        {
          qualifiedRegistrationNumbers,
          method: "manual",
        }
      );

      if (response.data.success) {
        onResultsUpdated?.();
        onClose();
      }
    } catch (error) {
      setError(error.response?.data?.message || "Failed to update results");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg max-w-4xl w-full mx-4 max-h-[90vh] overflow-hidden">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">
            Upload Round Results
          </h3>
          <p className="text-sm text-gray-600">
            {companyName} - {positionTitle}
          </p>
        </div>

        <div className="p-6">
          <div className="mb-6">
            <div className="flex space-x-4">
              <button
                className={`px-4 py-2 rounded-lg ${uploadMethod === "manual" ? "bg-blue-100 text-blue-700" : "bg-gray-100 text-gray-600"}`}
                onClick={() => setUploadMethod("manual")}
              >
                Manual Selection
              </button>
              <button
                className={`px-4 py-2 rounded-lg ${uploadMethod === "file" ? "bg-blue-100 text-blue-700" : "bg-gray-100 text-gray-600"}`}
                onClick={() => setUploadMethod("file")}
              >
                File Upload
              </button>
            </div>
          </div>

          {uploadMethod === "manual" ? (
            <div className="space-y-4">
              <div className="flex justify-between items-center">
                <p className="text-sm text-gray-600">
                  {students.length} students attended this round
                </p>
                <div className="space-x-2">
                  <button
                    className="text-sm text-blue-600 hover:text-blue-800"
                    onClick={() =>
                      setSelectedStudents(new Set(students.map((s) => s.id)))
                    }
                  >
                    Select All
                  </button>
                  <button
                    className="text-sm text-blue-600 hover:text-blue-800"
                    onClick={() => setSelectedStudents(new Set())}
                  >
                    Clear All
                  </button>
                </div>
              </div>

              <div className="max-h-96 overflow-y-auto border rounded-lg">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50 sticky top-0">
                    <tr>
                      <th className="px-4 py-2 text-left">
                        <input
                          type="checkbox"
                          checked={
                            selectedStudents.size === students.length &&
                            students.length > 0
                          }
                          onChange={() => {
                            if (selectedStudents.size === students.length) {
                              setSelectedStudents(new Set());
                            } else {
                              setSelectedStudents(
                                new Set(students.map((s) => s.id))
                              );
                            }
                          }}
                        />
                      </th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">
                        Name
                      </th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">
                        Registration
                      </th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">
                        CGPA
                      </th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {students.map((student) => (
                      <tr key={student.id} className="hover:bg-gray-50">
                        <td className="px-4 py-2">
                          <input
                            type="checkbox"
                            checked={selectedStudents.has(student.id)}
                            onChange={() => handleStudentToggle(student.id)}
                          />
                        </td>
                        <td className="px-4 py-2 text-sm text-gray-900">
                          {student.name}
                        </td>
                        <td className="px-4 py-2 text-sm text-gray-600">
                          {student.registration_number}
                        </td>
                        <td className="px-4 py-2 text-sm text-gray-600">
                          {student.cgpa || "N/A"}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          ) : (
            <div className="space-y-4">
              <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center">
                <FileText className="mx-auto h-12 w-12 text-gray-400" />
                <div className="mt-4">
                  <label htmlFor="file-upload" className="cursor-pointer">
                    <span className="mt-2 block text-sm font-medium text-gray-900">
                      Upload CSV file with qualified registration numbers
                    </span>
                    <input
                      id="file-upload"
                      type="file"
                      accept=".csv"
                      className="sr-only"
                      onChange={handleFileUpload}
                    />
                  </label>
                  <p className="mt-2 text-xs text-gray-500">
                    One registration number per line
                  </p>
                </div>
              </div>
            </div>
          )}
        </div>

        {error && (
          <div className="px-6 py-2 text-red-600 bg-red-50 text-sm">
            {error}
          </div>
        )}

        <div className="px-6 py-4 bg-gray-50 border-t border-gray-200 flex justify-between">
          <div className="text-sm text-gray-600">
            {uploadMethod === "manual" &&
              `${selectedStudents.size} of ${students.length} students selected`}
          </div>
          <div className="space-x-3">
            <button
              className="px-4 py-2 border border-gray-300 rounded-md text-sm font-medium text-gray-700 hover:bg-gray-50"
              onClick={onClose}
              disabled={loading}
            >
              Cancel
            </button>
            <button
              className="px-4 py-2 bg-blue-600 border border-transparent rounded-md text-sm font-medium text-white hover:bg-blue-700 disabled:opacity-50"
              onClick={handleSubmit}
              disabled={
                loading ||
                (uploadMethod === "manual" && selectedStudents.size === 0)
              }
            >
              {loading ? "Processing..." : "Update Results"}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

// Rounds Table Component
const RoundsTable = ({
  events,
  positionId,
  companyName,
  positionTitle,
  onUpdate,
}) => {
  const [showUploadModal, setShowUploadModal] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState(null);

  const handleBulkUpload = (eventId) => {
    setSelectedEvent(eventId);
    setShowUploadModal(true);
  };

  const getEventStatusIcon = (event) => {
    if (event.status === "completed")
      return <CheckCircle size={14} className="text-emerald-500" />;
    if (event.status === "ongoing")
      return <Clock size={14} className="text-blue-500" />;
    return <AlertCircle size={14} className="text-slate-400" />;
  };

  const getRoundTypeBadge = (roundType) => {
    const types = {
      technical: "bg-purple-50 text-purple-700 border-purple-200",
      hr: "bg-emerald-50 text-emerald-700 border-emerald-200",
      aptitude: "bg-blue-50 text-blue-700 border-blue-200",
      gd: "bg-orange-50 text-orange-700 border-orange-200",
      interview: "bg-indigo-50 text-indigo-700 border-indigo-200",
    };
    return types[roundType] || "bg-slate-50 text-slate-700 border-slate-200";
  };

  const calculatePercentage = (numerator, denominator) => {
    if (!denominator || denominator === 0) return "0";
    return Math.round((numerator / denominator) * 100);
  };

  return (
    <>
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-slate-200">
          <thead className="bg-slate-50">
            <tr>
              <th className="px-4 py-2.5 text-left text-xs font-semibold text-slate-700 uppercase tracking-wide">
                Round Details
              </th>
              <th className="px-4 py-2.5 text-left text-xs font-semibold text-slate-700 uppercase tracking-wide">
                <div className="flex items-center gap-1">
                  <Users size={12} />
                  Eligible
                </div>
              </th>
              <th className="px-4 py-2.5 text-left text-xs font-semibold text-slate-700 uppercase tracking-wide">
                <div className="flex items-center gap-1">
                  <TrendingUp size={12} />
                  Applied
                </div>
              </th>
              <th className="px-4 py-2.5 text-left text-xs font-semibold text-slate-700 uppercase tracking-wide">
                Attended
              </th>
              <th className="px-4 py-2.5 text-left text-xs font-semibold text-slate-700 uppercase tracking-wide">
                Qualified
              </th>
              <th className="px-4 py-2.5 text-left text-xs font-semibold text-slate-700 uppercase tracking-wide">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="divide-y divide-slate-100 bg-white">
            {events.map((event, index) => (
              <tr
                key={event.id}
                className="hover:bg-slate-50 transition-colors"
              >
                <td className="px-4 py-3">
                  <div className="flex items-start space-x-3">
                    <div className="flex-shrink-0">
                      <div className="h-7 w-7 rounded-lg bg-gradient-to-br from-blue-500 to-blue-600 flex items-center justify-center">
                        <span className="text-xs font-bold text-white">
                          {event.round_number || index + 1}
                        </span>
                      </div>
                    </div>
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center gap-1.5 mb-1">
                        <h3 className="text-sm font-semibold text-slate-900">
                          {event.title}
                        </h3>
                        {getEventStatusIcon(event)}
                      </div>
                      {event.event_date && (
                        <div className="flex items-center gap-1 text-xs text-slate-600 mt-1">
                          <Calendar size={11} className="text-slate-400" />
                          <span>
                            {new Date(event.event_date).toLocaleDateString(
                              "en-US",
                              {
                                month: "short",
                                day: "numeric",
                                year: "numeric",
                              }
                            )}
                          </span>
                        </div>
                      )}
                    </div>
                  </div>
                </td>
                <td className="px-4 py-3">
                  <div className="text-sm font-semibold text-slate-900">
                    {event.eligible_count || 0}
                  </div>
                </td>
                <td className="px-4 py-3">
                  <div>
                    <div className="text-sm font-semibold text-blue-600">
                      {event.applied_count || 0}
                    </div>
                    <div className="text-xs text-slate-500">
                      {calculatePercentage(
                        event.applied_count,
                        event.eligible_count
                      )}
                      %
                    </div>
                  </div>
                </td>
                <td className="px-4 py-3">
                  <div>
                    <div className="text-sm font-semibold text-amber-600">
                      {event.attended_count || 0}
                    </div>
                    <div className="text-xs text-slate-500">
                      {calculatePercentage(
                        event.attended_count,
                        event.eligible_count
                      )}
                      %
                    </div>
                  </div>
                </td>
                <td className="px-4 py-3">
                  <div>
                    <div className="text-sm font-semibold text-emerald-600">
                      {event.qualified_count || 0}
                    </div>
                    <div className="text-xs text-slate-500">
                      {calculatePercentage(
                        event.qualified_count,
                        event.attended_count
                      )}
                      %
                    </div>
                  </div>
                </td>
                <td className="px-4 py-3">
                  <div className="flex items-center gap-2">
                    <button
                      className="inline-flex items-center px-2.5 py-1.5 text-xs font-medium text-blue-700 bg-blue-50 rounded hover:bg-blue-100 transition-colors border border-blue-200"
                      onClick={() =>
                        window.open(`/events/${event.id}`, "_blank")
                      }
                    >
                      <Eye className="h-3.5 w-3.5 mr-1" />
                      View
                    </button>
                    <button
                      className="inline-flex items-center px-2.5 py-1.5 text-xs font-medium text-white bg-emerald-600 rounded hover:bg-emerald-700 transition-colors"
                      onClick={() => handleBulkUpload(event.id)}
                    >
                      <Upload className="h-3.5 w-3.5 mr-1" />
                      Upload
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {showUploadModal && (
        <ResultUploadModal
          eventId={selectedEvent}
          onClose={() => setShowUploadModal(false)}
          companyName={companyName}
          positionTitle={positionTitle}
          onResultsUpdated={onUpdate}
        />
      )}
    </>
  );
};

// Position Section Component
const PositionSection = ({ position, companyName, onUpdate }) => {
  const formatPackage = (amount) => {
    if (amount == null || isNaN(amount)) return "Not Disclosed";
    if (amount >= 10000000) return `₹ ${(amount / 10000000).toFixed(1)} Cr`;
    if (amount >= 100000) return `₹ ${(amount / 100000).toFixed(1)} LPA`;
    return `₹ ${amount.toLocaleString()}`;
  };

  const renderPackageInfo = () => {
    switch (position.job_type) {
      case "internship":
        return (
          <span className="text-xs text-slate-600">
            {formatPackage(position.internship_stipend_monthly)}/month
          </span>
        );
      case "full_time":
        return (
          <span className="text-xs text-slate-600">
            {formatPackage(position.package_range)}
          </span>
        );
      case "internship_plus_ppo":
        return (
          <>
            <span className="text-xs text-slate-600">
              {formatPackage(position.internship_stipend_monthly)}/month
            </span>
            <span className="text-xs text-slate-400">•</span>
            <span className="text-xs text-slate-600">
              PPO: {formatPackage(position.package_range)}
            </span>
          </>
        );
      default:
        return null;
    }
  };

  return (
    <div className="border-b border-slate-100 last:border-b-0">
      <div className="px-5 py-3 bg-blue-50/50">
        <div className="flex items-center justify-between">
          <div>
            <h4 className="text-sm font-semibold text-slate-900">
              {position.position_title}
            </h4>
            <div className="flex items-center space-x-3 mt-1">
              {renderPackageInfo()}
              <span className="text-xs text-slate-400">•</span>
              <span className="text-xs text-slate-600">
                {position.job_type?.replace(/_/g, " ").toUpperCase()}
              </span>
            </div>
          </div>
          <div className="text-right">
            <p className="text-xs text-slate-500">Selected</p>
            <p className="text-base font-semibold text-emerald-600">
              {position.final_selected_count || 0}
            </p>
          </div>
        </div>
      </div>

      {position.events && position.events.length > 0 ? (
        <RoundsTable
          events={position.events}
          positionId={position.id}
          companyName={companyName}
          positionTitle={position.position_title}
          onUpdate={onUpdate}
        />
      ) : (
        <div className="px-5 py-6 text-center text-slate-500">
          <Calendar size={32} className="mx-auto mb-2 text-slate-300" />
          <p className="text-sm">No rounds scheduled yet</p>
        </div>
      )}
    </div>
  );
};

// Company Card Component
const CompanyCard = ({ company, onToggle, isExpanded, onUpdate }) => {
  const calculateCompanyStatus = () => {
    let hasUpcoming = false;
    let hasOngoing = false;
    let hasCompleted = false;

    company.positions?.forEach((position) => {
      position.events?.forEach((event) => {
        if (event.status === "upcoming") hasUpcoming = true;
        if (event.status === "ongoing") hasOngoing = true;
        if (event.status === "completed") hasCompleted = true;
      });
    });

    if (hasOngoing) return "ongoing";
    if (hasUpcoming) return "upcoming";
    if (hasCompleted) return "completed";
    return "upcoming";
  };

  const status = calculateCompanyStatus();

  const getStatusColor = (status) => {
    switch (status) {
      case "ongoing":
        return "bg-blue-50 text-blue-700 border-blue-200";
      case "completed":
        return "bg-emerald-50 text-emerald-700 border-emerald-200";
      case "upcoming":
        return "bg-amber-50 text-amber-700 border-amber-200";
      default:
        return "bg-slate-50 text-slate-700 border-slate-200";
    }
  };

  const totalApplications =
    company.positions?.reduce((sum, pos) => {
      const firstRoundEvent = pos.events?.find((e) => e.round_number === 1);
      return sum + (firstRoundEvent?.applied_count || 0);
    }, 0) || 0;

  return (
    <div className="bg-white rounded-lg shadow-sm border border-slate-200 overflow-hidden hover:shadow-md transition-shadow">
      <div
        className="px-5 py-3 bg-gradient-to-r from-slate-50 to-white cursor-pointer hover:from-slate-100 hover:to-slate-50 transition-colors"
        onClick={() => onToggle(company.id)}
      >
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            {isExpanded ? (
              <ChevronDown size={18} className="text-slate-600" />
            ) : (
              <ChevronRight size={18} className="text-slate-600" />
            )}
            <div>
              <h3 className="text-base font-semibold text-slate-900">
                {company.company_name}
              </h3>
              <p className="text-xs text-slate-500 mt-0.5">
                {company.positions?.length || 0} positions
              </p>
            </div>
            <span
              className={`px-2.5 py-0.5 rounded-md text-xs font-medium border ${getStatusColor(status)}`}
            >
              {status.charAt(0).toUpperCase() + status.slice(1)}
            </span>
          </div>
          <div className="flex items-center space-x-4">
            <div className="text-right">
              <p className="text-xs text-slate-500">Applications</p>
              <p className="text-base font-semibold text-slate-900">
                {totalApplications}
              </p>
            </div>
          </div>
        </div>
      </div>

      {isExpanded && (
        <div className="border-t border-slate-200">
          {company.positions?.map((position) => (
            <PositionSection
              key={position.id}
              position={position}
              companyName={company.company_name}
              onUpdate={onUpdate}
            />
          ))}
        </div>
      )}
    </div>
  );
};

// Main Component
const RoundTrackingPage = () => {
  const [companies, setCompanies] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [expandedCompanies, setExpandedCompanies] = useState(new Set());
  const [filters, setFilters] = useState({
    search: "",
    status: "all",
    jobType: "all",
  });

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      setLoading(true);
      const { data } = await axios.get(
        `http://localhost:5000/round_tracking/companies/2022`
      );
      setCompanies(data.companies);
    } catch (error) {
      console.error("Error fetching data:", error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleCompanyToggle = (companyId) => {
    const newExpanded = new Set(expandedCompanies);
    if (newExpanded.has(companyId)) {
      newExpanded.delete(companyId);
    } else {
      newExpanded.add(companyId);
    }
    setExpandedCompanies(newExpanded);
  };

  const filteredCompanies = companies.filter((company) => {
    const matchesSearch =
      company.company_name
        .toLowerCase()
        .includes(filters.search.toLowerCase()) ||
      company.positions?.some((pos) =>
        pos.position_title.toLowerCase().includes(filters.search.toLowerCase())
      );

    // Calculate status for filtering
    let status = "upcoming";
    company.positions?.forEach((position) => {
      position.events?.forEach((event) => {
        if (event.status === "ongoing") status = "ongoing";
        else if (event.status === "completed" && status !== "ongoing")
          status = "completed";
      });
    });

    const matchesStatus = filters.status === "all" || status === filters.status;
    const matchesJobType =
      filters.jobType === "all" ||
      company.positions?.some((pos) => pos.job_type === filters.jobType);

    return matchesSearch && matchesStatus && matchesJobType;
  });

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading round data...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 flex items-center justify-center">
        <div className="bg-red-50 border border-red-200 rounded-xl p-6 max-w-md w-full">
          <h3 className="text-lg font-semibold text-red-800 mb-2">
            Error Loading Data
          </h3>
          <p className="text-red-600">{error}</p>
          <button
            onClick={fetchData}
            className="mt-4 px-4 py-2 bg-red-100 text-red-700 rounded-lg hover:bg-red-200 transition-colors"
          >
            Try Again
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100">
      <div className="max-w-7xl mx-auto p-6">
        {/* Header */}
        <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6 mb-6">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
            <div className="flex items-start gap-4">
              <button
                onClick={() => window.history.back()}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <ArrowLeft className="h-6 w-6 text-gray-600" />
              </button>
              <div>
                <h1 className="text-3xl font-bold text-gray-900 mb-2">
                  Round Tracking
                </h1>
                <p className="text-gray-600">
                  Monitor student progress through interview rounds
                </p>
              </div>
            </div>

            <div className="flex items-center gap-3">
              <button className="bg-green-600 hover:bg-green-700 text-white px-6 py-2.5 rounded-lg flex items-center gap-2 transition-all duration-200 shadow-sm hover:shadow-md">
                <Download className="h-4 w-4" />
                Export Report
              </button>
            </div>
          </div>

          <StatsCards />
        </div>

        {/* Filters */}
        <FilterBar filters={filters} onFiltersChange={setFilters} />

        {/* Companies List */}
        <div className="space-y-4">
          {filteredCompanies.length > 0 ? (
            filteredCompanies.map((company) => (
              <CompanyCard
                key={company.id}
                company={company}
                onToggle={handleCompanyToggle}
                isExpanded={expandedCompanies.has(company.id)}
                onUpdate={fetchData}
              />
            ))
          ) : (
            <div className="bg-white rounded-lg shadow-sm p-12 text-center">
              <Calendar size={48} className="mx-auto mb-4 text-gray-300" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                No Companies Found
              </h3>
              <p className="text-gray-600">
                {filters.search ||
                filters.status !== "all" ||
                filters.jobType !== "all"
                  ? "Try adjusting your filters to see more results."
                  : "No companies have scheduled rounds yet."}
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default RoundTrackingPage;
